import React, { useState } from "react";
import { View, Text, Modal, Platform, TouchableOpacity } from "react-native";
import { CONSTANTS } from "../utils/constants";
import CustomPicker from "../components/CustomPicker";

export default function TimePicker({ visible, onClose, onDone }) {
  const [min, setMin] = useState("select");
  const [hr, setHr] = useState("select");
  const [type, setType] = useState("AM");

  const onPressDone = () => {
    if (hr == "select") {
      alert("Select hour");
      return;
    }
    if (min == "select") {
      alert("Select minute");
      return;
    }
    if (type == "select") {
      alert("Select type");
      return;
    }
    onClose();
    onDone(`${hr}:${min} ${type}`);
  };

  return (
    <Modal
      transparent={true}
      animationType="slide"
      visible={visible}
      onRequestClose={() => onClose()}
    >
      <View
        style={{
          backgroundColor: "rgba(0, 0, 0, 0.5)",
          width: "100%",
          height: "100%",
          justifyContent: "center",
          alignItems: "center",
        }}
      >
        <View
          style={{
            backgroundColor: CONSTANTS.COLORS.white,
            borderRadius: 10,
            padding: 10,
            width: "90%",
            alignItems: "center",
            justifyContent: "center",
            maxWidth: 390,
          }}
        >
          <Text style={{ fontSize: 12 }}>{"PICK TIME"}</Text>
          <View
            style={{
              flexDirection: "row",
              justifyContent: "space-between",
              width: "100%",
              marginTop: 10,
            }}
          >
            <View
              style={{
                width: "30%",
                borderWidth: Platform.OS == "web" ? 0 : 1,
                height: 48,
                borderRadius: 5,
                borderColor: CONSTANTS.COLORS.lightGrey,
              }}
            >
              <CustomPicker
                style={{
                  width: "30%",
                  height: 45,
                  borderRadius: 5,
                  paddingLeft: 10,
                  backgroundColor: "white",
                }}
                placeholder={{ label: "HOUR", value: "select" }}
                data={CONSTANTS.HOURS}
                val={hr}
                setValue={(val) => setHr(val)}
              />
            </View>
            <View
              style={{
                width: "30%",
                borderWidth: Platform.OS == "web" ? 0 : 1,
                height: 48,
                borderRadius: 5,
                borderColor: CONSTANTS.COLORS.lightGrey,
              }}
            >
              <CustomPicker
                style={{
                  width: "30%",
                  height: 45,
                  borderRadius: 5,
                  paddingLeft: 10,
                  backgroundColor: "white",
                }}
                placeholder={{ label: "MIN", value: "select" }}
                data={CONSTANTS.MINS}
                val={min}
                setValue={(val) => setMin(val)}
              />
            </View>
            <View
              style={{
                width: "30%",
                borderWidth: Platform.OS == "web" ? 0 : 1,
                height: 48,
                borderRadius: 5,
                borderColor: CONSTANTS.COLORS.lightGrey,
              }}
            >
              <CustomPicker
                style={{
                  width: "30%",
                  height: 45,
                  borderRadius: 5,
                  paddingLeft: 10,
                  backgroundColor: "white",
                }}
                placeholder={{ label: "AM/PM", value: "select" }}
                data={CONSTANTS.range}
                val={type}
                setValue={(val) => setType(val)}
              />
            </View>
          </View>
          <TouchableOpacity
            onPress={() => onPressDone()}
            style={{
              height: 40,
              width: 100,
              marginTop: 10,
              borderRadius: 5,
              justifyContent: "center",
              alignItems: "center",
              backgroundColor: CONSTANTS.COLORS.primary,
            }}
          >
            <Text style={{ color: CONSTANTS.COLORS.white, fontWeight: "bold" }}>
              DONE
            </Text>
          </TouchableOpacity>
        </View>
      </View>
    </Modal>
  );
}
