import React, { useEffect, useLayoutEffect, useState } from "react";
import { View, Text, FlatList } from "react-native";
import common_axios from "../../utils/url";
import Stream from "./stream";
import Feather from "@expo/vector-icons/Feather";
import { CONSTANTS } from "../../utils/constants";
import Add from "../../modals/add";

export default function MainScheduler({ navigation, route }) {
  const id = route?.params?.id;
  useLayoutEffect(() => {
    navigation.setOptions({
      headerTitle: `${route.params?.dates[0]}  -  ${
        route.params?.dates[route.params?.dates?.length - 1]
      }`,
    });
  }, []);

  const [stream, setStream] = useState([]);
  const [visible, setVisible] = useState(false);

  useEffect(() => {
    getStreams();
  }, []);

  const getStreams = async () => {
    const { data } = await common_axios.get(`/schedule/${id}`);
    console.log(data);
    if (data.status == "ok") {
      setStream(data.data);
    }
  };

  return (
    <View style={{ height: "100%", width: "100%" }}>
      <FlatList
        data={stream}
        ListFooterComponent={() => (
          <Feather
            name="plus-circle"
            size={30}
            onPress={() => setVisible(true)}
            style={{ alignSelf: "flex-end", marginRight: 15, marginTop: 15 }}
            color={CONSTANTS.COLORS.black}
          />
        )}
        keyExtractor={(item) => item.id}
        renderItem={({ item }) => <Stream item={item} />}
      />
      <Add
        visible={visible}
        data={{
          title: "Add Streamer",
          id: `${id}`,
          dates: route.params?.dates,
          from: route.params?.dates[0],
          to: route.params?.dates[route.params?.dates?.length - 1],
        }}
        onClose={() => setVisible(false)}
        onDone={(data) => setStream(data)}
      />
    </View>
  );
}
