import React, { useEffect, useState } from "react";
import { View, Text, Modal, Platform } from "react-native";
import { CONSTANTS } from "../utils/constants";
import common_axios from "../utils/url";
import CustomPicker from "../components/CustomPicker";
import Button from "../components/Button";

export default function Assign({ visible, onClose, data }) {
  const [presenters, setPresenters] = useState([]);
  const [selected, setSelected] = useState("");

  useEffect(() => {
    getData();
  }, []);

  const getData = async () => {
    const { data: res } = await common_axios.get("/user/presenter");
    if (res.status == "ok") {
      const arr = [];
      res.data?.forEach((item) => {
        arr.push({ label: item.username, value: item.id });
      });
      setPresenters(arr);
    }
  };

  const assign = async () => {
    if (selected == "select") {
      alert("Please select a presenter");
      return;
    }
    const { data: res } = await common_axios.post("/stream", {
      presenter_id: selected,
      stream_data: data,
      stream_id: data.slugId,
      time: new Date(data.startTime).toLocaleString(),
      streamer_id: data.hostDetails.channel,
    });
    console.log(res);
    if (res.status == "ok") {
      onClose();
    }
  };
  return (
    <Modal
      transparent={true}
      animationType="slide"
      visible={visible}
      onRequestClose={() => onClose()}
    >
      <View
        style={{
          backgroundColor: "rgba(0, 0, 0, 0.5)",
          width: "100%",
          height: "100%",
          justifyContent: "center",
          alignItems: "center",
        }}
      >
        <View
          style={{
            backgroundColor: CONSTANTS.COLORS.white,
            borderRadius: 10,
            padding: 10,
            width: "90%",
            alignItems: "center",
            justifyContent: "center",
          }}
        >
          <Text style={{ fontSize: 12 }}>{data.title}</Text>
          <View
            style={{
              width: "90%",
              borderWidth: Platform.OS == "web" ? 0 : 1,
              height: 48,
              marginTop: 10,
              borderRadius: 5,
              borderColor: CONSTANTS.COLORS.lightGrey,
            }}
          >
            <CustomPicker
              style={{
                width: "100%",
                height: 45,
                borderRadius: 5,
                paddingLeft: 10,
                backgroundColor: "white",
              }}
              data={presenters}
              placeholder={{ label: "Select Presenter", value: "select" }}
              val={selected}
              setValue={setSelected}
            />
          </View>
          <Button style={{ marginTop: 15 }} onPress={assign} title={"Assign"} />
        </View>
      </View>
    </Modal>
  );
}
