import React, { useEffect, useState } from "react";
import { View, Text } from "react-native";
import { Calendar } from "react-native-calendars";
import { useDispatch } from "react-redux";
import { getStreamers } from "../../../redux/actions/main";
import Button from "../../components/Button";
import { CONSTANTS } from "../../utils/constants";

export default function DatePicker({ navigation }) {
  const [selected, setSelected] = useState({});
  const dispatch = useDispatch();
  useEffect(() => {
    dispatch(getStreamers());
  }, []);

  const onDayPress = (day) => {
    console.log(selected);
    if (selected[day]?.selected) {
      const ss = {
        ...selected,
        [day]: {
          selected: false,
          marked: false,
          selectedColor: CONSTANTS.COLORS.primary,
        },
      };
      setSelected(ss);
      return;
    }
    const ss = {
      ...selected,
      [day]: {
        selected: true,
        marked: true,
        selectedColor: CONSTANTS.COLORS.primary,
      },
    };
    setSelected(ss);
  };

  const onPressNext = () => {
    const arr = [];
    Object.keys(selected).map((item) => {
      arr.push(item);
    });
    navigation.navigate(CONSTANTS.SCREENS.mainScheduler, {
      dates: arr,
      id: `${arr[0]}-${arr[arr?.length - 1]}`,
    });
  };
  return (
    <View
      style={{
        height: "100%",
        width: "100%",
        justifyContent: "center",
        alignItems: "center",
      }}
    >
      <View style={{ width: "90%", maxWidth: 400 }}>
        <Calendar
          markedDates={selected}
          onDayPress={(day) => {
            onDayPress(day.dateString);
          }}
          monthFormat={"MM yyyy"}
          enableSwipeMonths={true}
          minDate={new Date().toString()}
        />
        <Button
          onPress={onPressNext}
          style={{ alignSelf: "center", marginTop: 15 }}
          title="NEXT"
        />
      </View>
    </View>
  );
}
