import React, { useEffect, useState } from "react";
import { View, Text, FlatList, TouchableOpacity } from "react-native";
import { CONSTANTS } from "../../utils/constants";
import common_axios from "../../utils/url";
import AntDesign from "@expo/vector-icons/AntDesign";

export default function Schedules({ navigation }) {
  const [data, setData] = useState([]);

  useEffect(() => {
    fetch_data();
  }, []);
  const fetch_data = async () => {
    const { data } = await common_axios.get("/schedule");
    if (data.status == "ok") {
      setData(data.data);
    }
  };

  return (
    <View style={{ height: "100%", width: "100%", padding: 10 }}>
      <AntDesign
        onPress={() => navigation.navigate(CONSTANTS.SCREENS.datePicker)}
        style={{ position: "absolute", right: 15, bottom: 15, zIndex: 5 }}
        name="pluscircle"
        color={CONSTANTS.COLORS.primary}
        size={45}
      />
      <FlatList
        data={data}
        keyExtractor={(item) => item}
        renderItem={({ item }) => (
          <TouchableOpacity
            onPress={() =>
              navigation.navigate(CONSTANTS.SCREENS.scheduled, {
                id: item,
              })
            }
            style={{
              width: "100%",
              paddingVertical: 8,
              paddingHorizontal: 15,
              backgroundColor: CONSTANTS.COLORS.white,
              borderRadius: 5,
              marginBottom: 10,
            }}
          >
            <Text>{item}</Text>
          </TouchableOpacity>
        )}
      />
    </View>
  );
}
