import { NavigationContainer } from "@react-navigation/native";
import { createNativeStackNavigator } from "@react-navigation/native-stack";
import { CONSTANTS } from "./src/utils/constants";
import Login from "./src/screens/Login";
import { useDispatch, useSelector } from "react-redux";
import { useEffect, useRef, useState } from "react";
import { getStreams, getUser } from "./redux/actions/main";
import { createMaterialTopTabNavigator } from "@react-navigation/material-top-tabs";
import Today from "./src/screens/Home/today";
import Tomorrow from "./src/screens/Home/tomorrow";
import Upcoming from "./src/screens/Home/upcoming";
import AsyncStorage from "@react-native-async-storage/async-storage";
import { ActivityIndicator, Platform, View } from "react-native";
import jwt_decode from "jwt-decode";
import { createBottomTabNavigator } from "@react-navigation/bottom-tabs";
import MyStream from "./src/screens/MyStream";
import Feather from "@expo/vector-icons/Feather";
import StreamDetails from "./src/screens/StreamDetsils";
import Profile from "./src/screens/Profile";
import Register from "./src/screens/Register";
import * as Notifications from "expo-notifications";
import common_axios from "./src/utils/url";
import Constants from "expo-constants";
import { SafeAreaView } from "react-native-safe-area-context";
import Scheduler from "./src/screens/Scheduler";
import MainScheduler from "./src/screens/MainScheduler";
import DatePicker from "./src/screens/MainScheduler/datePicker";
import ScheduleDetail from "./src/screens/MainScheduler/detail";
import MySchedule from "./src/screens/MainScheduler/mySchedule";
import Response from "./src/screens/MainScheduler/response";
import Schedules from "./src/screens/Schedules";
import Scheduled from "./src/screens/Scheduled";

Notifications.setNotificationHandler({
  handleNotification: async () => ({
    shouldShowAlert: true,
    shouldPlaySound: true,
    shouldSetBadge: true,
  }),
});

export default function Entry() {
  const AuthStack = createNativeStackNavigator();
  const HomeStack = createNativeStackNavigator();
  const Tab = createMaterialTopTabNavigator();
  const BottomTab = createBottomTabNavigator();
  const notificationListener = useRef();
  const responseListener = useRef();

  const { is_user, user_data } = useSelector((state) => state.reducer);

  const dispatch = useDispatch();

  useEffect(() => {
    dispatch(getStreams());
  }, []);
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    checkLoginState();
  }, [is_user]);

  useEffect(() => {
    if (Platform.OS !== "web") {
      registerForPushNotificationsAsync().then(async (token) => {
        if (token) {
          console.log(token);
          const { data } = await common_axios.post("/user/token/add", {
            token,
          });
          console.log(data);
          if (data.msg == "Invalid token.") {
            logOut();
          }
          console.log(data);
        }
      });

      // This listener is fired whenever a notification is received while the app is foregrounded
      notificationListener.current =
        Notifications.addNotificationReceivedListener((notification) => {
          console.log(notification);
        });

      // This listener is fired whenever a user taps on or interacts with a notification (works when app is foregrounded, backgrounded, or killed)
      responseListener.current =
        Notifications.addNotificationResponseReceivedListener((response) => {
          console.log(response);
        });

      return () => {
        Notifications.removeNotificationSubscription(
          notificationListener.current
        );
        Notifications.removeNotificationSubscription(responseListener.current);
      };
    }
  }, []);

  const checkLoginState = async () => {
    try {
      const value = await AsyncStorage.getItem("access_token");
      if (value != null) {
        const access_token = JSON.parse(value);
        var decoded = jwt_decode(access_token);
        if (decoded.exp < Date.now() / 1000) {
          refresh();
        } else {
          dispatch({ type: "IS_USER", data: true });
          dispatch(getUser());
          setLoading(false);
        }
      } else {
        dispatch({ type: "IS_USER", data: false });
        setLoading(false);
      }
    } catch (e) {
      console.log(e);
      await AsyncStorage.removeItem("access_token");
      checkLoginState();
    }
  };

  const logOut = async () => {
    await AsyncStorage.removeItem("access_token");
    await AsyncStorage.removeItem("refresh_token");
    dispatch({ type: "IS_USER", data: false });
    setLoading(false);
  };

  const refresh = async () => {
    try {
      const value = await AsyncStorage.getItem("refresh_token");
      if (value != null) {
        const refresh_token = JSON.parse(value);
        const { data } = await common_axios.post(`/auth/refresh`, {
          refresh_token,
        });

        if (data?.status == "ok") {
          const access_token = JSON.stringify(data.access_token);
          await AsyncStorage.setItem("access_token", access_token);
          dispatch({ type: "IS_USER", data: true });
          setLoading(false);
        } else {
          logOut();
        }
      } else {
        dispatch({ type: "IS_USER", data: false });
        setLoading(false);
      }
    } catch (e) {
      await AsyncStorage.removeItem("refresh_token");
    }
  };

  if (loading) {
    return (
      <View
        style={{
          justifyContent: "center",
          alignItems: "center",
          height: "100%",
          width: "100%",
        }}
      >
        <ActivityIndicator size="large" color={CONSTANTS.COLORS.primary} />
      </View>
    );
  }

  const MyTabs = () => {
    return (
      <Tab.Navigator
        screenOptions={{
          tabBarIndicatorStyle: { backgroundColor: CONSTANTS.COLORS.primary },
        }}
      >
        <Tab.Screen name="Today" component={Today} />
        <Tab.Screen name="Tomorrow" component={Tomorrow} />
        <Tab.Screen name="Upcoming" component={Upcoming} />
      </Tab.Navigator>
    );
  };

  const BottomNav = () => (
    <BottomTab.Navigator>
      <BottomTab.Screen
        name={"TabHome"}
        component={MyTabs}
        options={{
          header: () => null,
          tabBarIcon: ({ focused, color }) => (
            <Feather name="home" size={24} color={color} />
          ),
          title: "Home",
        }}
      />
      {user_data?.type != "user" && (
        <BottomTab.Screen
          name={"TabStream"}
          component={MyStream}
          options={{
            header: () => null,
            tabBarIcon: ({ focused, color }) => (
              <Feather name="meh" size={24} color={color} />
            ),
            title: "My Streams",
          }}
        />
      )}
      <BottomTab.Screen
        name={"TabProfile"}
        component={Profile}
        options={{
          header: () => null,
          tabBarIcon: ({ focused, color }) => (
            <Feather name="user" size={24} color={color} />
          ),
          title: "Profile",
        }}
      />
    </BottomTab.Navigator>
  );

  const Auth = () => (
    <AuthStack.Navigator>
      <AuthStack.Screen name={CONSTANTS.SCREENS.login} component={Login} />
      <AuthStack.Screen
        name={CONSTANTS.SCREENS.register}
        component={Register}
      />
    </AuthStack.Navigator>
  );

  const Main = () => (
    <HomeStack.Navigator>
      <HomeStack.Screen
        options={{ header: () => null }}
        name={CONSTANTS.SCREENS.home}
        component={BottomNav}
      />
      <HomeStack.Screen
        name={CONSTANTS.SCREENS.streamDetails}
        component={StreamDetails}
      />
      <HomeStack.Screen
        name={CONSTANTS.SCREENS.scheduler}
        component={Scheduler}
      />
      <HomeStack.Screen
        options={{ headerTitle: "SCHEDULE" }}
        name={CONSTANTS.SCREENS.mainScheduler}
        component={MainScheduler}
      />
      <HomeStack.Screen
        options={{ headerTitle: "PICK DATE" }}
        name={CONSTANTS.SCREENS.datePicker}
        component={DatePicker}
      />
      <HomeStack.Screen
        options={{ headerTitle: "SCHEDULE DETAIL" }}
        name={CONSTANTS.SCREENS.scheduleDetail}
        component={ScheduleDetail}
      />
      <HomeStack.Screen
        options={{ headerTitle: "MY SCHEDULE" }}
        name={CONSTANTS.SCREENS.mySchedule}
        component={MySchedule}
      />
      <HomeStack.Screen
        options={{ headerTitle: "RESPOND" }}
        name={CONSTANTS.SCREENS.respond}
        component={Response}
      />
      <HomeStack.Screen
        options={{ headerTitle: "SCHEDULES" }}
        name={CONSTANTS.SCREENS.schedules}
        component={Schedules}
      />
      <HomeStack.Screen
        options={{ headerTitle: "SCHEDULED" }}
        name={CONSTANTS.SCREENS.scheduled}
        component={Scheduled}
      />
    </HomeStack.Navigator>
  );

  return (
    <NavigationContainer>{is_user ? <Main /> : <Auth />}</NavigationContainer>
  );
}

async function registerForPushNotificationsAsync() {
  let token;
  if (Constants.isDevice) {
    const { status: existingStatus } =
      await Notifications.getPermissionsAsync();
    let finalStatus = existingStatus;
    if (existingStatus !== "granted") {
      const { status } = await Notifications.requestPermissionsAsync();
      finalStatus = status;
    }
    if (finalStatus !== "granted") {
      console.log("Failed to get push token for push notification!");
      return;
    }
    token = (await Notifications.getExpoPushTokenAsync()).data;
  } else {
    console.log("Must use physical device for Push Notifications");
  }

  if (Platform.OS === "android") {
    Notifications.setNotificationChannelAsync("default", {
      name: "default",
      importance: Notifications.AndroidImportance.MAX,
      vibrationPattern: [0, 250, 250, 250],
      lightColor: "#FF231F7C",
    });
  }

  return token;
}
