import React, { useEffect, useState } from "react";
import { View, Text } from "react-native";

export default function ScheduleDetail({ navigation, route }) {
  const d = route.params?.data ? route.params?.data : {};
  const [data, setData] = useState({});
  const [loading, setLoading] = useState(true);
  useEffect(() => {
    setData(d);
    setLoading(false);
  }, [d]);

  if (loading) {
    return null;
  }
  return (
    <View
      style={{
        height: "100%",
        width: "100%",
        padding: 10,
      }}
    >
      {Object.entries(data.availablity).map(([key, item]) => {
        return (
          <View
            key={key}
            style={{
              width: "100%",
              borderRadius: 5,
              padding: 5,
              borderWidth: 1,
              borderColor: "#c4c4c4",
              marginBottom: 10,
            }}
          >
            <Text style={{ fontSize: 17, fontWeight: "bold" }}>
              Streaming on {key}?
            </Text>
            <Text
              style={{
                marginTop: 5,
                color:
                  item.available == "nd"
                    ? "orange"
                    : item.available == "Yes"
                    ? "green"
                    : "red",
              }}
            >
              {item.available == "nd"
                ? "Not responded"
                : item.available == "Other"
                ? item.reason
                : item.available}
            </Text>
          </View>
        );
      })}
    </View>
  );
}
