import axios from "axios";
import common_axios from "../../src/utils/url";
import { IS_USER, LOGOUT_MODAL, STREAMERS, STREAMS, USER_DATA } from "./types";

export const setIsUser = (val) => ({
  type: IS_USER,
  data: val,
});

export const setUserData = (val) => ({
  type: USER_DATA,
  data: val,
});

export const setLogoutModal = (val) => ({
  type: LOGOUT_MODAL,
  data: val,
});

export const getStreams = () => async (dispatch) => {
  try {
    const { data } = await common_axios.get("/stream/upcoming/data");
    if (data.status == "ok") {
      dispatch({ type: STREAMS, data: data.data });
    }
  } catch (e) {
    console.log(e);
  }
};

export const getUser = () => async (dispatch) => {
  try {
    const { data } = await common_axios.get("/user");
    console.log(data, "gg");
    dispatch({ type: USER_DATA, data: data.data });
  } catch (e) {
    console.log(e);
  }
};

export const getStreamers = () => async (dispatch) => {
  try {
    const { data } = await common_axios.get("/user/streamer");
    if (data.status === "ok") {
      const arr = [];
      data.data.forEach((item) => {
        arr.push({ label: item.username, value: item.id });
      });
      dispatch({ type: STREAMERS, data: arr });
    }
  } catch (e) {
    console.log(e);
  }
};
