import { ToastAndroid, Alert, Platform } from "react-native";

function Toast(text) {
  Platform.OS != "android"
    ? Alert.alert(text)
    : ToastAndroid.showWithGravityAndOffset(
        text,
        ToastAndroid.LONG,
        ToastAndroid.BOTTOM,
        25,
        300
      );
}

export default Toast;
