import React, { useEffect, useState } from "react";
import {
  View,
  Text,
  Modal,
  Platform,
  TextInput,
  TouchableOpacity,
} from "react-native";
import { CONSTANTS } from "../utils/constants";
import common_axios from "../utils/url";
import CustomPicker from "../components/CustomPicker";
import Button from "../components/Button";
import TimePicker from "./time";
import Entypo from "@expo/vector-icons/Entypo";

export default function Add({ visible, onClose, data, onDone }) {
  const [startTime, setStartTime] = useState("");
  const [endTime, setEndTime] = useState("");
  const [webPeekLink, setWebPeekLink] = useState("");
  const [tilte, setTitle] = useState("");
  const [glance_id, setGlanceId] = useState("");
  const [game, setGame] = useState("");
  const [name_of_the_influencer, setNoi] = useState("");
  const [channel_id, setChannel_id] = useState("");
  const [duration, setDuaration] = useState("");
  const [streamers, setStreamers] = useState([]);
  const [timePicker, setTimePicker] = useState({ visible: false, type: null });
  const [availablity, setAvailablity] = useState({});
  useEffect(() => {
    getStreamers();
  }, []);

  const getStreamers = async () => {
    const { data } = await common_axios.get("/user/streamer");
    if (data.status === "ok") {
      const arr = [];
      data.data.forEach((item) => {
        arr.push({ label: item.username, value: item.id });
      });
      setStreamers(arr);
    }
  };

  useEffect(() => {
    let objj = {};
    data.dates?.forEach((item) => {
      objj = {
        ...objj,
        [item]: {
          available: "nd",
          reason: "",
        },
      };
    });
    setAvailablity(objj);
  }, [data]);

  const onTimeDone = (time) => {
    if (timePicker.type == "start") {
      setStartTime(time);
    } else if (timePicker.type == "end") {
      setEndTime(time);
    }
  };

  const add = async () => {
    if (tilte.length < 10) {
      alert("Enter a valid title");
      return;
    }

    if (glance_id.length < 3) {
      alert("Enter a valid glance id");
      return;
    }

    if (duration.length == 0) {
      alert("Enter a valid duration");
      return;
    }

    if (webPeekLink.length < 5) {
      alert("Enter a valid webpeeklink");
      return;
    }

    if (name_of_the_influencer.length < 2) {
      alert("Enter a influencer name");
      return;
    }

    if (channel_id.length == 0 || channel_id == "select") {
      alert("Select a valid streamer");
      return;
    }

    if (game.length == 0 || game == "select") {
      alert("Select a valid game");
      return;
    }

    if (startTime.length == 0) {
      alert("Enter a valid start time");
      return;
    }

    if (endTime.length == 0) {
      alert("Enter a valid end time");
      return;
    }

    const { data: res } = await common_axios.post("/schedule", {
      schedule_id: data.id,
      startTime,
      endTime,
      description: webPeekLink,
      tilte,
      glance_id,
      game,
      name_of_the_influencer,
      channel_id,
      duration,
      availablity,
      from: data.from,
      to: data.to,
    });
    console.log(res);
    if (res.status == "ok") {
      onClose();
      onDone(res.data);
    } else {
      alert(res.msg);
    }
  };

  return (
    <Modal
      transparent={true}
      animationType="slide"
      visible={visible}
      onRequestClose={() => onClose()}
    >
      <TimePicker
        onDone={(val) => onTimeDone(val)}
        onClose={() => setTimePicker({ visible: false, type: null })}
        visible={timePicker.visible}
      />
      <View
        style={{
          backgroundColor: "rgba(0, 0, 0, 0.5)",
          width: "100%",
          height: "100%",
          justifyContent: "center",
          alignItems: "center",
        }}
      >
        <View
          style={{
            backgroundColor: CONSTANTS.COLORS.white,
            borderRadius: 10,
            padding: 10,
            width: "90%",
            alignItems: "center",
            justifyContent: "center",
            maxWidth: 390,
            borderWidth: 2,
            borderColor: CONSTANTS.COLORS.primary,
          }}
        >
          <Entypo
            name="cross"
            size={24}
            onPress={() => onClose()}
            style={{ position: "absolute", top: 10, right: 10 }}
          />
          <Text style={{ fontSize: 16, fontWeight: "bold" }}>{data.title}</Text>
          <View style={{ width: "100%", marginTop: 15 }}>
            <TextInput
              style={{
                height: 60,
                width: "100%",
                borderColor: "#c4c4c4",
                borderWidth: 1,
                borderRadius: 5,
                padding: 5,
                textAlignVertical: "center",
              }}
              placeholder={"Enter title"}
              value={tilte}
              multiline={true}
              onChangeText={(text) => setTitle(text)}
            />
            <View
              style={{
                flexDirection: "row",
                justifyContent: "space-between",
                marginTop: 10,
              }}
            >
              <TextInput
                style={{
                  height: 40,
                  width: "48%",
                  borderColor: "#c4c4c4",
                  borderWidth: 1,
                  borderRadius: 5,
                  padding: 5,
                  textAlignVertical: "center",
                }}
                placeholder={"Enter glance id"}
                value={glance_id}
                onChangeText={(text) => setGlanceId(text)}
              />
              <TextInput
                style={{
                  height: 40,
                  width: "48%",
                  borderColor: "#c4c4c4",
                  borderWidth: 1,
                  borderRadius: 5,
                  padding: 5,
                  textAlignVertical: "center",
                }}
                keyboardType="phone-pad"
                placeholder={"Enter duration"}
                value={duration}
                onChangeText={(text) => setDuaration(text)}
              />
            </View>
            <TextInput
              style={{
                height: 60,
                width: "100%",
                borderColor: "#c4c4c4",
                borderWidth: 1,
                borderRadius: 5,
                marginTop: 10,
                padding: 5,
                textAlignVertical: "center",
              }}
              placeholder={"Enter description"}
              value={webPeekLink}
              multiline={true}
              onChangeText={(text) => setWebPeekLink(text)}
            />
            <TextInput
              style={{
                height: 40,
                width: "100%",
                borderColor: "#c4c4c4",
                borderWidth: 1,
                borderRadius: 5,
                marginTop: 10,
                padding: 5,
                textAlignVertical: "center",
              }}
              placeholder={"Enter influencer name"}
              value={name_of_the_influencer}
              onChangeText={(text) => setNoi(text)}
            />
            <View
              style={{
                flexDirection: "row",
                justifyContent: "space-between",
                marginTop: 10,
              }}
            >
              {streamers && streamers.length > 0 && (
                <View
                  style={{
                    width: "48%",
                    borderWidth: Platform.OS == "web" ? 0 : 1,
                    height: 48,
                    borderRadius: 5,
                    borderColor: CONSTANTS.COLORS.lightGrey,
                  }}
                >
                  <CustomPicker
                    style={{
                      width: "48%",
                      height: 45,
                      borderRadius: 5,
                      paddingLeft: 10,
                      backgroundColor: "white",
                    }}
                    placeholder={{ label: "Select streamer", value: "select" }}
                    data={streamers}
                    val={channel_id}
                    setValue={(val) => setChannel_id(val)}
                  />
                </View>
              )}
              <View
                style={{
                  width: "48%",
                  borderWidth: Platform.OS == "web" ? 0 : 1,
                  height: 48,
                  borderRadius: 5,
                  borderColor: CONSTANTS.COLORS.lightGrey,
                }}
              >
                <CustomPicker
                  style={{
                    width: "48%",
                    height: 45,
                    borderRadius: 5,
                    paddingLeft: 10,
                    backgroundColor: "white",
                  }}
                  placeholder={{ label: "Select game", value: "select" }}
                  data={CONSTANTS.GAMES}
                  val={game}
                  setValue={(val) => setGame(val)}
                />
              </View>
            </View>
            <View
              style={{
                flexDirection: "row",
                justifyContent: "space-between",
                marginTop: 10,
              }}
            >
              <TouchableOpacity
                onPress={() => setTimePicker({ visible: true, type: "start" })}
                style={{
                  width: "48%",
                  height: 40,
                  borderRadius: 4,
                  borderWidth: 1,
                  borderColor: "#c4c4c4",
                  justifyContent: "center",
                  alignItems: "center",
                }}
              >
                <Text
                  style={{
                    color:
                      startTime && startTime.length > 0 ? "black" : "#c4c4c4",
                  }}
                >
                  {startTime && startTime.length > 0
                    ? startTime
                    : "Start Time?"}
                </Text>
              </TouchableOpacity>
              <TouchableOpacity
                onPress={() => setTimePicker({ visible: true, type: "end" })}
                style={{
                  width: "48%",
                  height: 40,
                  borderRadius: 4,
                  borderWidth: 1,
                  borderColor: "#c4c4c4",
                  justifyContent: "center",
                  alignItems: "center",
                }}
              >
                <Text
                  style={{
                    color: endTime && endTime.length > 0 ? "black" : "#c4c4c4",
                  }}
                >
                  {endTime && endTime.length > 0 ? endTime : "End Time?"}
                </Text>
              </TouchableOpacity>
            </View>
            <Button
              style={{ marginTop: 10, alignSelf: "center" }}
              onPress={() => add()}
              title="ADD"
            />
          </View>
        </View>
      </View>
    </Modal>
  );
}
