import AsyncStorage from "@react-native-async-storage/async-storage";
import React, { useState } from "react";
import { View, Image, Text } from "react-native";
import { SafeAreaView } from "react-native-safe-area-context";
import { useDispatch } from "react-redux";
import { getUser, setIsUser } from "../../../redux/actions/main";
import Button from "../../components/Button";
import InputField from "../../components/InputField";
import { CONSTANTS } from "../../utils/constants";
import Toast from "../../utils/ToastAndroid";
import common_axios from "../../utils/url";

export default function Register({ navigation }) {
  const [username, setUsername] = useState("");
  const [password, setPassword] = useState("");
  const [email, setEmail] = useState("");
  const [phone, setPhone] = useState("");
  const dispatch = useDispatch();

  const onRegister = async () => {
    let reg = /^\w+([.-]?\w+)*@\w+([.-]?\w+)*(\.\w{2,3})+$/;

    if (username.length == 0) {
      Toast("Enter a valid username");
      return;
    }

    if (password.length < 6) {
      Toast("Enter a valid password");
      return;
    }

    if (email.length < 2 || !reg.test(email)) {
      Toast("Enter a valid email");
      return;
    }

    if (phone.length < 10) {
      Toast("Enter a valid phone number");
      return;
    }

    try {
      const { data } = await common_axios.post("/auth/register", {
        username,
        password,
        email,
        phone,
        type: "user",
        role: "user",
      });
      if (data.status == "ok") {
        await AsyncStorage.setItem(
          "access_token",
          JSON.stringify(data.data.access_token)
        );
        await AsyncStorage.setItem(
          "refresh_token",
          JSON.stringify(data.data.refresh_token)
        );
        dispatch(getUser());
        dispatch({ type: "IS_USER", data: true });
      } else {
        Toast(data.msg);
      }
    } catch (e) {
      console.log(e);
    }
  };

  return (
    <SafeAreaView
      style={{
        height: "100%",
        width: "100%",
      }}
    >
      <View
        style={{
          height: "100%",
          width: "100%",
          justifyContent: "center",
          alignItems: "center",
        }}
      >
        <View
          style={{
            width: "90%",
            maxWidth: 400,
            padding: 15,
            shadowColor: CONSTANTS.COLORS.black,
            shadowOffset: { height: 3, width: 3 },
            shadowOpacity: 0.5,
            shadowRadius: 3,
            backgroundColor: CONSTANTS.COLORS.white,
            borderRadius: 10,
            justifyContent: "center",
            alignItems: "center",
          }}
        >
          <Image
            style={{ height: 30, width: 100, resizeMode: "contain" }}
            source={CONSTANTS.IMAGES.logo}
          />
          <InputField
            value={username}
            placeholder="Enter Username"
            setValue={setUsername}
            style={{ marginTop: 15 }}
          />
          <InputField
            value={password}
            style={{ marginTop: 15 }}
            placeholder="Enter Password"
            setValue={setPassword}
            secureTextEntry={true}
          />
          <InputField
            value={email}
            placeholder="Enter Email"
            setValue={setEmail}
            style={{ marginTop: 15 }}
          />
          <InputField
            value={phone}
            placeholder="Enter Phone"
            setValue={setPhone}
            style={{ marginTop: 15 }}
          />
          <Button
            onPress={() => onRegister()}
            style={{ marginTop: 15 }}
            title={"Register"}
          />
          <Text
            onPress={() => navigation.navigate(CONSTANTS.SCREENS.login)}
            style={{
              fontSize: 12,
              textDecorationLine: "underline",
              marginTop: 10,
            }}
          >
            Already have an account?
          </Text>
        </View>
      </View>
    </SafeAreaView>
  );
}
