import React, { useEffect, useState } from "react";
import { View, Text, FlatList, Dimensions } from "react-native";
import common_axios from "../../utils/url";
import Feather from "@expo/vector-icons/Feather";
import { CONSTANTS } from "../../utils/constants";
import { Picker } from "@react-native-picker/picker";
import CustomPicker from "../../components/CustomPicker";
import { Platform } from "react-native";

export default function Scheduler() {
  const [streamers, setStreamers] = useState([]);
  const [picked, setPicked] = useState([]);

  useEffect(() => {
    getStreamers();
  }, []);

  const getStreamers = async () => {
    const { data } = await common_axios.get("/user/streamer");
    if (data.status === "ok") {
      const arr = [];
      data.data.forEach((item) => {
        arr.push({ label: item.username, value: item.id });
      });
      setStreamers(arr);
    }
  };

  const addOne = () => {
    setPicked((state) => [
      ...state,
      {
        id: Math.floor(100000 + Math.random() * 900000),
        streamer_id: "select",
        game: "",
        duration: "",
        slot: "",
      },
    ]);
  };

  const addStreamer = (streamer, index) => {
    let newArr = [...picked];
    newArr[index].streamer_id = streamer;
    console.log(newArr);
    setPicked(newArr);
  };

  const addDuration = (streamer, index) => {
    let newArr = [...picked];
    newArr[index].duration = streamer;
    setPicked(newArr);
  };

  const addSlot = (streamer, index) => {
    let newArr = [...picked];
    newArr[index].slot = streamer;
    setPicked(newArr);
  };

  const addGame = (streamer, index) => {
    let newArr = [...picked];
    newArr[index].game = streamer;
    setPicked(newArr);
  };

  console.log(streamers);

  return (
    <View style={{ height: "100%", width: "100%" }}>
      <Text style={{ textAlign: "center", marginTop: 10 }}>AUTO SCHEDULER</Text>
      <View style={{ flex: 1 }}>
        <FlatList
          data={picked}
          keyExtractor={(item) => item.id}
          ListFooterComponent={() => (
            <Feather
              name="plus-circle"
              size={30}
              onPress={() => addOne()}
              style={{ alignSelf: "flex-end", marginRight: 15, marginTop: 15 }}
              color={CONSTANTS.COLORS.black}
            />
          )}
          renderItem={({ item, index }) => (
            <View
              style={{
                width: Dimensions.get("window").width * 0.95,
                justifyContent: "space-evenly",
                alignItems: "center",
                alignSelf: "center",
                marginTop: 10,
                borderRadius: 5,
                height: 48,
                flexDirection: "row",
                backgroundColor: CONSTANTS.COLORS.lightGrey,
              }}
            >
              {streamers && streamers.length > 0 && (
                <View
                  style={{
                    width: "25%",
                    borderWidth: Platform.OS == "web" ? 0 : 1,
                    height: 48,
                    borderRadius: 5,
                    borderColor: CONSTANTS.COLORS.lightGrey,
                  }}
                >
                  <CustomPicker
                    style={{
                      width: "25%",
                      height: 45,
                      borderRadius: 5,
                      paddingLeft: 10,
                      backgroundColor: "white",
                    }}
                    placeholder={{ label: "Select streamer", value: "select" }}
                    data={streamers}
                    val={item.streamer_id}
                    setValue={(val) => addStreamer(val, index)}
                  />
                </View>
              )}
              <View
                style={{
                  width: "25%",
                  borderWidth: Platform.OS == "web" ? 0 : 1,
                  height: 48,
                  marginTop: 0,
                  borderRadius: 5,
                  borderColor: CONSTANTS.COLORS.lightGrey,
                }}
              >
                <CustomPicker
                  style={{
                    width: "25%",
                    height: 45,
                    borderRadius: 5,
                    paddingLeft: 10,
                    backgroundColor: "white",
                  }}
                  placeholder={{ label: "Select duration", value: "select" }}
                  data={CONSTANTS.DURATION}
                  val={item.duration}
                  setValue={(val) => addDuration(val, index)}
                />
              </View>
              <View
                style={{
                  width: "25%",
                  borderWidth: Platform.OS == "web" ? 0 : 1,
                  height: 48,
                  marginTop: 0,
                  borderRadius: 5,
                  borderColor: CONSTANTS.COLORS.lightGrey,
                }}
              >
                <CustomPicker
                  style={{
                    width: "25%",
                    height: 45,
                    borderRadius: 5,
                    paddingLeft: 10,
                    backgroundColor: "white",
                  }}
                  placeholder={{ label: "Select slot", value: "select" }}
                  data={CONSTANTS.SLOT}
                  val={item.slot}
                  setValue={(val) => addSlot(val, index)}
                />
              </View>
              <View
                style={{
                  width: "25%",
                  borderWidth: Platform.OS == "web" ? 0 : 1,
                  height: 48,
                  marginTop: 0,
                  borderRadius: 5,
                  borderColor: CONSTANTS.COLORS.lightGrey,
                }}
              >
                <CustomPicker
                  style={{
                    width: "25%",
                    height: 45,
                    borderRadius: 5,
                    paddingLeft: 10,
                    backgroundColor: "white",
                  }}
                  placeholder={{ label: "Select game", value: "select" }}
                  data={CONSTANTS.GAMES}
                  val={item.game}
                  setValue={(val) => addGame(val, index)}
                />
              </View>
            </View>
          )}
        />
      </View>
    </View>
  );
}
