import React, { useEffect, useLayoutEffect, useState } from "react";
import { View, Text, FlatList, TouchableOpacity } from "react-native";
import common_axios from "../../utils/url";
import Stream from "./stream";
import Feather from "@expo/vector-icons/Feather";
import { CONSTANTS } from "../../utils/constants";
import Add from "../../modals/add";
import Button from "../../components/Button";

export default function MySchedule({ navigation, route }) {
  //   useLayoutEffect(() => {
  //     navigation.setOptions({
  //       headerTitle: `${route.params?.dates[0]}  -  ${
  //         route.params?.dates[route.params?.dates?.length - 1]
  //       }`,
  //     });
  //   }, []);

  const [stream, setStream] = useState([]);

  useEffect(() => {
    getStreams();
  }, []);

  const getStreams = async () => {
    const { data } = await common_axios.get("/schedule/stream/my");
    console.log(data);
    if (data.status == "ok") {
      setStream(data.data);
    }
  };

  return (
    <View style={{ height: "100%", width: "100%", padding: 10 }}>
      <FlatList
        data={stream}
        keyExtractor={(item) => item.id}
        renderItem={({ item }) => (
          <TouchableOpacity
            style={{
              width: "100%",
              padding: 10,
              borderRadius: 5,
              borderWidth: 1,
              borderColor: item.responded ? "green" : "red",
              flexDirection: "row",
              justifyContent: "space-between",
              alignItems: "center",
              marginBottom: 10,
            }}
          >
            <View>
              <Text>From : {item.from}</Text>
              <Text>
                To {"     "}: {item.to}
              </Text>
            </View>
            <Button
              title={item.status == "active" ? "RESPOND" : "EXPIRED"}
              onPress={() =>
                navigation.navigate(CONSTANTS.SCREENS.respond, { data: item })
              }
              titleStyle={{ fontSize: 12 }}
              style={{
                width: 100,
                height: 30,
                borderRadius: 5,
                backgroundColor:
                  item.status == "active"
                    ? CONSTANTS.COLORS.primary
                    : "#c4c4c4",
              }}
            />
          </TouchableOpacity>
        )}
      />
    </View>
  );
}
