import React, { useEffect, useState } from "react";
import {
  View,
  Text,
  TouchableOpacity,
  TextInput,
  ScrollView,
  Alert,
} from "react-native";
import { useSelector } from "react-redux";
import Button from "../../components/Button";
import { CONSTANTS } from "../../utils/constants";
import common_axios from "../../utils/url";

export default function Response({ navigation, route }) {
  const d = route.params?.data ? route.params?.data : {};
  const { user_data } = useSelector((state) => state.reducer);
  console.log(user_data);
  const [data, setData] = useState({});
  const [avl, setAvl] = useState({});
  const [loading, setLoading] = useState(true);
  useEffect(() => {
    setData(d);
    setAvl(d.availablity);
    setLoading(false);
  }, [d]);

  const onClickYes = (key) => {
    let d = avl[key];
    d.available = "Yes";
    setAvl({ ...avl, [key]: d });
  };

  const onClickNo = (key) => {
    let d = avl[key];
    d.available = "No";
    setAvl({ ...avl, [key]: d });
  };

  const onClickOther = (key) => {
    let d = avl[key];
    d.available = "Other";
    setAvl({ ...avl, [key]: d });
  };

  const onClickOff = (key) => {
    let d = avl[key];
    d.available = "Off";
    setAvl({ ...avl, [key]: d });
  };

  const setReason = (text, key) => {
    let d = avl[key];
    d.reason = text;
    setAvl({ ...avl, [key]: d });
  };

  const submit = () => {
    const arr = [];
    Object.entries(avl).map(([key, item]) => {
      arr.push({ day: key, ...item });
    });
    let error = null;
    for (let i in arr) {
      if (arr[i].available == "nd") {
        alert(`Not responded for ${arr[i].day}`);
        error = true;
        break;
      }
      if (arr[i].available == "Other" && arr[i]?.reason?.length == 0) {
        alert(`Please specify the reason for ${arr[i].day}`);
        error = true;
        break;
      }
    }
    if (error) return;
    Alert.alert("Hey streamer!", "Do you want to proceed further?", [
      {
        text: "Cancel",
        onPress: () => console.log("Cancel Pressed"),
        style: "cancel",
      },
      { text: "OK", onPress: () => onComplete() },
    ]);
  };

  const onComplete = async () => {
    const { data: res } = await common_axios.post("/schedule/stream/respond", {
      id: data.id,
      data: avl,
      name: user_data.username,
    });
    if (res.status == "ok") {
      navigation.goBack(null);
    }
  };

  if (loading) {
    return null;
  }
  return (
    <View
      style={{
        height: "100%",
        width: "100%",
        padding: 10,
      }}
    >
      <ScrollView>
        {Object.entries(avl).map(([key, item]) => {
          return (
            <View
              key={key}
              style={{
                width: "100%",
                borderRadius: 5,
                padding: 5,
                borderWidth: 1,
                borderColor: "#c4c4c4",
                marginBottom: 10,
              }}
            >
              <Text style={{ fontWeight: "bold" }}>
                Are you available to stream on
              </Text>
              <Text style={{ fontWeight: "bold" }}>
                {new Date(key).getDate()}-{new Date(key).getMonth() + 1}-
                {new Date(key).getFullYear()}(
                {CONSTANTS.DAY[new Date(key).getDay()]}) on your regular time
                slot?
              </Text>
              <RespBtn
                title={"Yes"}
                selected={item.available == "Yes"}
                item={item}
                key_prop={key}
                disabled={
                  CONSTANTS.DAY[new Date(key).getDay()] == user_data.off_day
                }
                onClick={(k) => onClickYes(k)}
              />
              <RespBtn
                disabled={
                  CONSTANTS.DAY[new Date(key).getDay()] == user_data.off_day
                }
                title={"No"}
                selected={item.available == "No"}
                item={item}
                key_prop={key}
                onClick={(k) => onClickNo(k)}
              />
              <RespBtn
                title={"Off Day"}
                selected={item.available == "Off"}
                disabled={
                  CONSTANTS.DAY[new Date(key).getDay()] != user_data.off_day
                }
                item={item}
                key_prop={key}
                onClick={(k) => onClickOff(k)}
              />
              <RespBtn
                title={"Other"}
                selected={item.available == "Other"}
                disabled={
                  CONSTANTS.DAY[new Date(key).getDay()] == user_data.off_day
                }
                item={item}
                key_prop={key}
                onClick={(k) => onClickOther(k)}
              />
              {item.available == "Other" && (
                <TextInput
                  placeholder="Please specify the reason."
                  value={item.reason}
                  multiline={true}
                  onChangeText={(text) => setReason(text, key)}
                  style={{
                    width: "100%",
                    height: 60,
                    borderRadius: 5,
                    marginTop: 10,
                    borderWidth: 1,
                    borderColor: "#c4c4c4",
                    paddingHorizontal: 10,
                    backgroundColor: CONSTANTS.COLORS.white,
                  }}
                />
              )}
            </View>
          );
        })}
        <Button
          disabled={data.responded || data.status != "active"}
          onPress={() => submit()}
          style={{ alignSelf: "center" }}
          title={
            data.responded
              ? "RESPONDED"
              : data.status != "active"
              ? "EXPIRED"
              : "SUBMIT"
          }
        />
      </ScrollView>
    </View>
  );
}

const RespBtn = ({ onClick, title, item, disabled, key_prop, selected }) => (
  <TouchableOpacity
    disabled={disabled}
    onPress={() => onClick(key_prop)}
    style={{
      flexDirection: "row",
      alignItems: "center",
      marginTop: 10,
      backgroundColor: CONSTANTS.COLORS.white,
      padding: 3,
      paddingHorizontal: 6,
      borderRadius: 5,
      alignSelf: "flex-start",
    }}
  >
    <View
      style={{
        height: 24,
        width: 24,
        backgroundColor: "#c4c4c4",
        borderRadius: 20,
        justifyContent: "center",
        alignItems: "center",
      }}
    >
      <View
        style={{
          height: 18,
          width: 18,
          backgroundColor: selected
            ? CONSTANTS.COLORS.primary
            : CONSTANTS.COLORS.white,
          borderRadius: 20,
        }}
      />
    </View>
    <Text
      style={{
        marginLeft: 5,
        color: disabled ? "grey" : CONSTANTS.COLORS.black,
      }}
    >
      {title}
    </Text>
  </TouchableOpacity>
);
