import React from "react";
import { View, Text, TouchableOpacity, Image } from "react-native";
import { SafeAreaView } from "react-native-safe-area-context";
import Mi from "@expo/vector-icons/MaterialIcons";
import { CONSTANTS } from "../../utils/constants";
import AsyncStorage from "@react-native-async-storage/async-storage";
import { useDispatch, useSelector } from "react-redux";
import { setIsUser } from "../../../redux/actions/main";
import Button from "../../components/Button";

export default function Profile({ navigation }) {
  const dispatch = useDispatch();
  const { user_data } = useSelector((state) => state.reducer);

  const logOut = async () => {
    await AsyncStorage.clear();
    dispatch(setIsUser(false));
  };

  return (
    <View style={{ height: "100%", width: "100%" }}>
      <Mi
        onPress={() => logOut()}
        name="logout"
        color={CONSTANTS.COLORS.black}
        size={30}
        style={{ position: "absolute", top: 15, right: 15, zIndex: 5 }}
      />
      <View style={{ height: "100%", width: "100%", alignItems: "center" }}>
        <Image
          style={{
            height: 100,
            width: 100,
            borderRadius: 50,
            resizeMode: "contain",
            marginTop: 30,
          }}
          source={CONSTANTS.IMAGES.small_logo}
        />
        <Text
          style={{
            fontSize: 22,
            marginTop: 10,
            fontWeight: "bold",
          }}
        >
          @{user_data?.username}
        </Text>
        <Text
          style={{
            fontSize: 16,
            marginTop: 8,
          }}
        >
          {user_data?.email}
        </Text>
        <Text
          style={{
            fontSize: 16,
            marginTop: 8,
          }}
        >
          {user_data?.phone}
        </Text>
        <View
          style={{
            padding: 5,
            backgroundColor: CONSTANTS.COLORS.primary,
            borderRadius: 5,
            marginTop: 8,
          }}
        >
          <Text
            style={{
              fontSize: 12,
              color: CONSTANTS.COLORS.white,
              fontWeight: "bold",
            }}
          >
            {user_data?.role?.toUpperCase()}
          </Text>
        </View>
        <View
          style={{
            height: 40,
            width: "100%",
            justifyContent: "space-between",
            flexDirection: "row",
            marginTop: 15,
          }}
        >
          <TouchableOpacity
            style={{
              width: "50%",
              height: "100%",
              justifyContent: "center",
              alignItems: "center",
              borderColor: CONSTANTS.COLORS.lightGrey,
              borderRightWidth: 3,
            }}
          >
            <Text>Total Presented</Text>
            <Text style={{ marginTop: 4, fontWeight: "bold" }}>
              {user_data?.total_presented}
            </Text>
          </TouchableOpacity>
          <TouchableOpacity
            style={{
              width: "50%",
              height: "100%",
              alignItems: "center",
              justifyContent: "center",
            }}
          >
            <Text>Total streamed</Text>
            <Text style={{ marginTop: 4, fontWeight: "bold" }}>
              {user_data?.total_streamed}
            </Text>
          </TouchableOpacity>
        </View>
        {user_data.role == "presenter" && (
          <View style={{ width: "100%", alignItems: "center", marginTop: 15 }}>
            <Button
              onPress={() => navigation.navigate(CONSTANTS.SCREENS.schedules)}
              title="Schedule"
            />
          </View>
        )}
        {user_data.role == "streamer" && (
          <View style={{ width: "100%", alignItems: "center", marginTop: 15 }}>
            <Button
              onPress={() => navigation.navigate(CONSTANTS.SCREENS.mySchedule)}
              title="My Schedule"
            />
          </View>
        )}
      </View>
    </View>
  );
}
