import React from "react";
import { TextInput } from "react-native";
import { GlobalStyles } from "../../utils/globalStyles";

export default function InputField({ value, setValue, style, ...props }) {
  return (
    <TextInput
      value={value}
      autoCapitalize="none"
      onChangeText={(text) => setValue(text)}
      style={{
        height: 45,
        width: "100%",
        borderRadius: 5,
        borderWidth: 1,
        borderColor: "#c4c4c4",
        paddingHorizontal: 10,
        ...GlobalStyles.regular_text,
        ...style,
      }}
      {...props}
    />
  );
}
