import axios from "axios";
import React, { useEffect, useState } from "react";
import {
  View,
  Text,
  Image,
  Linking,
  TouchableOpacity,
  Dimensions,
  Platform,
} from "react-native";
import { SafeAreaView } from "react-native-safe-area-context";
import { CONSTANTS } from "../../utils/constants";
import Feather from "@expo/vector-icons/Feather";
import Ionicons from "@expo/vector-icons/Ionicons";
import Button from "../../components/Button";
import { useSelector } from "react-redux";
import Assign from "../../modals/assign";

export default function StreamDetails({ route, navigation }) {
  const [data, setData] = useState({});
  const [visible, setVisible] = useState(false);
  const { user_data } = useSelector((state) => state.reducer);
  useEffect(() => {
    getData();
  }, []);
  const getData = async () => {
    try {
      const { data } = await axios.get(
        `http://live.c.glanceapis.com/v1/public-shows/${route.params.id}`
      );
      setData(data);
    } catch (e) {
      console.log(e);
    }
  };
  return (
    <View>
      <View style={{ height: Dimensions.get("window").height, width: "100%" }}>
        <TouchableOpacity
          onPress={() => navigation.goBack(null)}
          style={{ position: "absolute", top: 10, left: 10, zIndex: 1 }}
        >
          <Feather
            name="chevron-left"
            size={35}
            color={CONSTANTS.COLORS.white}
          />
        </TouchableOpacity>
        <Image
          source={{ uri: data.poster }}
          style={{
            width: "100%",
            height: Dimensions.get("window").height * 0.8,
            resizeMode: Platform.OS == "web" ? "contain" : "cover",
          }}
        />
        <View style={{ flex: 1, padding: 10 }}>
          <View
            style={{
              padding: 8,
              borderColor: CONSTANTS.COLORS.lightGrey,
              borderWidth: 0.8,
              borderRadius: 5,
              flexDirection: "row",
              alignItems: "center",
              justifyContent: "space-between",
            }}
          >
            <View
              style={{
                width: "13%",
                justifyContent: "center",
                alignItems: "center",
              }}
            >
              <Image
                source={{ uri: data?.hostDetails?.profileImage }}
                style={{
                  width: 40,
                  height: 40,
                  borderRadius: 20,
                  backgroundColor: CONSTANTS.COLORS.lightGrey,
                }}
              />
            </View>
            <View style={{ width: "85%" }}>
              <Text
                numberOfLines={1}
                style={{ fontSize: 16, fontWeight: "bold" }}
              >
                {data.title}
              </Text>
              <View
                style={{
                  flexDirection: "row",
                  justifyContent: "space-between",
                  alignItems: "center",
                  marginRight: 8,
                  marginTop: 5,
                }}
              >
                <Text style={{ fontSize: 12, fontWeight: "400" }}>
                  {data?.hostDetails?.name}
                </Text>
                <View style={{ flexDirection: "row", alignItems: "center" }}>
                  <Feather name="clock" size={12} />
                  <Text style={{ fontSize: 12, marginLeft: 5 }}>
                    {new Date(data.startTime).getHours() > 9
                      ? new Date(data.startTime).getHours() +
                        ":" +
                        (new Date(data.startTime).getMinutes() > 9
                          ? new Date(data.startTime).getMinutes()
                          : "0" + new Date(data.startTime).getMinutes())
                      : "0" +
                        new Date(data.startTime).getHours() +
                        ":" +
                        (new Date(data.startTime).getMinutes() > 9
                          ? new Date(data.startTime).getMinutes()
                          : "0" + new Date(data.startTime).getMinutes())}
                  </Text>
                </View>
                <View style={{ flexDirection: "row", alignItems: "center" }}>
                  <Ionicons name="md-timer-outline" size={12} />
                  <Text style={{ fontSize: 12, marginLeft: 5 }}>
                    {data.durationInSeconds / 3600 < 1
                      ? `${parseFloat(data.durationInSeconds / 60).toFixed(
                          2
                        )} mins`
                      : `${parseFloat(data.durationInSeconds / 3600).toFixed(
                          2
                        )} hrs`}
                  </Text>
                </View>
              </View>
            </View>
          </View>
        </View>
        <View
          style={{
            position: "absolute",
            bottom: 10,
            left: 10,
            right: 10,
            flexDirection: "row",
            alignItems: "center",
            justifyContent: "space-between",
          }}
        >
          <Button
            onPress={() => Linking.openURL(data.url)}
            style={{ width: user_data.type == "admin" ? "48%" : "100%" }}
            title="WATCH"
          />
          {user_data.type == "admin" && (
            <Button
              onPress={() => setVisible(true)}
              style={{ width: "48%", backgroundColor: "#16B54D" }}
              title="ASSIGN"
            />
          )}
        </View>
      </View>
      <Assign visible={visible} data={data} onClose={() => setVisible(false)} />
    </View>
  );
}
