import React, { useEffect, useLayoutEffect, useState } from "react";
import { View, FlatList, TouchableOpacity } from "react-native";
import common_axios from "../../utils/url";
import Stream from "../MainScheduler/stream";
import MaterialCommunityIcons from "@expo/vector-icons/MaterialCommunityIcons";
import { CONSTANTS } from "../../utils/constants";
import axios from "axios";

export default function Scheduled({ navigation, route }) {
  const id = route?.params?.id;

  useLayoutEffect(() => {
    navigation.setOptions({
      headerTitle: id,
    });
  }, []);

  const [stream, setStream] = useState([]);

  useEffect(() => {
    getStreams();
  }, []);

  console.log(stream.length > 0 && stream[0]);

  const getStreams = async () => {
    const { data } = await common_axios.get(`/schedule/${id}`);
    if (data.status == "ok") {
      setStream(data.data);
    }
  };

  const submitHandler = async (dat) => {
    try {
      const { data: res } = await axios.get(
        "https://sheet.best/api/sheets/ed24bebd-8c9e-49fa-86be-fcce0e9dfa78"
      );

      res.forEach(async (element, index) => {
        const { data } = await axios.delete(
          `https://sheet.best/api/sheets/ed24bebd-8c9e-49fa-86be-fcce0e9dfa78/${index}`
        );
      });

      const duplicate = [];

      stream.forEach((val) => {
        Object.entries(val.availablity).map(([key, item]) => {
          if (key === dat && item.available == "Yes") {
            duplicate.push({
              Game: val.game,
              Name_of_the_Influencer: val.name_of_the_influencer,
              Channel_Id: val.channel_id,
              Glance_ID: item.glance_id,
              Time_Slot_in_24_hrs: `${val.startTime}-${val.endTime}`,
              Time: val.duration,
              Title: val.tilte,
              Discription: val.description,
              Webpeek_Link: "",
            });
          }
        });
      });

      if (duplicate.length != 0) {
        const { data } = await axios.post(
          "https://sheet.best/api/sheets/ed24bebd-8c9e-49fa-86be-fcce0e9dfa78",
          duplicate
        );
        console.log(data);
      }
    } catch (e) {
      console.log(e);
    }
  };

  return (
    <View style={{ height: "100%", width: "100%" }}>
      <FlatList
        data={stream}
        keyExtractor={(item) => item.id}
        renderItem={({ item }) => <Stream item={item} />}
      />
      <TouchableOpacity
        onPress={() => submitHandler("2022-05-11")}
        style={{
          height: 50,
          width: 50,
          borderRadius: 25,
          backgroundColor: CONSTANTS.COLORS.white,
          position: "absolute",
          bottom: 10,
          right: 10,
          zIndex: 1,
          justifyContent: "center",
          alignItems: "center",
          borderColor: CONSTANTS.COLORS.primary,
          borderWidth: 1,
        }}
      >
        <MaterialCommunityIcons
          name="google-spreadsheet"
          size={25}
          color={CONSTANTS.COLORS.black}
        />
      </TouchableOpacity>
    </View>
  );
}
